import { h, Host } from "@stencil/core";
export class RaulTimelineEvent {
    constructor() {
        this.maxHeight = 30;
        this.shouldTruncate = false;
        this.truncated = false;
    }
    handleViewButtonClick() {
        this.truncated = !this.truncated;
    }
    componentDidLoad() {
        if (this.titleEl) {
            this.truncated = this.shouldTruncate = this.titleEl.scrollHeight > this.maxHeight;
        }
        if (this.subTitleEl && !this.shouldTruncate) {
            this.truncated = this.shouldTruncate = this.subTitleEl.scrollHeight > this.maxHeight;
        }
    }
    render() {
        return (h(Host, { class: { 'r-timeline__event--truncate': this.truncated } },
            this.data.title.text &&
                h("div", { class: "r-timeline__content__title", ref: el => this.titleEl = el },
                    this.data.title.user &&
                        h("a", { href: this.data.title.user.url, class: "r-timeline__content__title__link", onClick: e => this.data.title.user.onClick(e) },
                            this.data.title.user.name,
                            " "),
                    this.data.title.text),
            this.data.subTitle &&
                h("div", { class: "r-timeline__content__sub-title", ref: el => this.subTitleEl = el }, this.data.subTitle),
            this.shouldTruncate &&
                h("button", { class: "r-timeline__content__view-more__link", onClick: () => this.handleViewButtonClick() }, this.truncated ? 'View More' : 'View Less'),
            this.data.timestamp &&
                h("div", { class: "r-timeline__content__timestamp" }, this.data.timestamp)));
    }
    static get is() { return "raul-timeline-event"; }
    static get properties() { return {
        "data": {
            "type": "unknown",
            "mutable": false,
            "complexType": {
                "original": "TimelineEventInterface",
                "resolved": "TimelineEventInterface",
                "references": {
                    "TimelineEventInterface": {
                        "location": "import",
                        "path": "../../../../utils/interfaces"
                    }
                }
            },
            "required": false,
            "optional": false,
            "docs": {
                "tags": [],
                "text": ""
            }
        }
    }; }
    static get states() { return {
        "shouldTruncate": {},
        "truncated": {}
    }; }
}
